  public  showLoading() {
    this.loadingController.create({
      message: '',
      cssClass:'custom',
      spinner:'dots'
    }).then((res) => {
      res.present();
 
      res.onDidDismiss().then((dis) => {
        console.log('Loading dismissed! after 2 Seconds');
      });
    });
    
  }

  public  hideLoading() {
    this.loadingController.dismiss();
  }


